.menuham{
    left: 50%;
    -webkit-transform: translate(-5%,0%);
            transform: translate(-5%,0%);
    width: 71px;
    height: 65px;
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;
}
.menuham li{
  list-style: none;
  position: absolute;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 70%;  height: 5px;
  background: #262626;
  transition: 1s;
}
.menuham li:nth-of-type(1){
  top: 30%;  left: 15%; 
  transition-delay: 1s; 
  opacity: 1; 
}
.menuham li:nth-of-type(2){
  top: 50%;  left: 15%; 
  transition-delay: 0s;   
}
.menuham li:nth-of-type(3){
  top: 70%;  right: 15%;
  transition-delay: 1s; 
  opacity: 1; 
}
.menuham.active li:nth-of-type(1){
  left: 100%;
  transition-delay: 0s;
  opacity: 0; 
}
.menuham.active li:nth-of-type(2){
  -webkit-transform: translateY(-50%) rotate(405deg);
          transform: translateY(-50%) rotate(405deg);
  transition-delay: .5s;
}
.menuham.active li:nth-of-type(3){
  right: 100%;
  transition-delay: 0s;
  opacity: 0; 
}




.menuham li:nth-of-type(2)::before{
 content: '';
 position: absolute;
 top: -150px;  left: 0;
 width: 100%;  height: 100%;
 background: #262626;
 -webkit-transform: rotate(90deg);
         transform: rotate(90deg);
 transition: 1s;
 transition-delay: .5s;
}
.menuham.active li:nth-of-type(2)::before{
 top: 0; 
 transition-delay: 1s;
}

@media (min-width:901px)
{
  .menuham
  {
    display:none;
  }
}
